<template>
  <b-modal
    id="EditContractDate"
    cancel-variant="outline-secondary"
    ok-title="تحديث"
    cancel-title="الغاء"
    centered
    size="sm"
    title="تعديل التصنيف"
    @ok="update"
  >
    <validation-observer ref="simpleRules">
      <b-form>
        <b-row>
          <!-- date -->
          <b-col md="12">
            <b-form-group
              label="اسم التصنيف"
              label-for="name"
            >
              <validation-provider
                #default="{ errors }"
                name="name"
                rules="required"
              >
                <b-form-input
                  id="name"
                  v-model="dataDate"
                  type="date"
                  :state="errors.length > 0 ? false:null"
                />
                <small class="text-danger">{{ errors[0] }}</small>
              </validation-provider>
            </b-form-group>
          </b-col>
        </b-row>
      </b-form>
    </validation-observer>
  </b-modal>
</template>

<script>
import { ValidationProvider, ValidationObserver, localize } from 'vee-validate'
import { required } from '@/@core/utils/validations/validations'
import {
  BFormInput, BFormGroup, BForm, BRow, BCol, BModal,
} from 'bootstrap-vue'
import ToastificationContent from '@core/components/toastification/ToastificationContent.vue'

const dictionary = {
  ar: {
    names: {
      name: 'اسم التصنيف',
      type: 'نوع التصنيف',
      image: 'صورة التصنيف',
    },
  },
}
localize('ar', dictionary.ar)
export default {
  name: 'EditContractDate',
  components: {
    ValidationProvider,
    ValidationObserver,
    BRow,
    BCol,
    BForm,
    BFormGroup,
    BFormInput,
    BModal,
    // eslint-disable-next-line vue/no-unused-components
    ToastificationContent,
  },
  props: {
    id: {
      type: Number,
      default: 0,
    },
  },
  data() {
    return {
      dataId: '',
      dataDate: '',
      required,
    }
  },
  watch: {
    id() {
      // const {
      //   id,
      // } = JSON.parse(JSON.stringify(this.obj))
      this.dataId = this.id
    },
  },
  methods: {
    update() {
      const date = this.dataDate
      this.$http.put(`user/contracts/update/date/${this.dataId}`, { quran_date: date }).then(() => {
        this.$bvModal.hide('EditContractDate')
        this.$toast({
          component: ToastificationContent,
          props: {
            title: 'تم تعديل التاريخ بنجاح',
            icon: 'CheckCircleIcon',
            variant: 'success',
          },
        })
      })
    },
  },
}
</script>

