<template>
  <div>
    <b-card
      ref="cardAction"
      :title="$t('pages.newContracts.title')"
    >
      <b-row>
        <b-col cols="12">
          <b-table
            id="my-table"
            :per-page="perPage"
            :current-page="currentPage"
            head-variant
            hover
            responsive
            :items="getContracts"
            :fields="fields"
            bordered
          >
            <template #head()="data">
              <span
                class="text-primary"
                style="font-size:18px"
              >{{ data.label }}</span>
            </template>

            <template #table-busy>
              <div class="text-center text-dark my-2">
                <b-spinner class="align-middle" />
                <strong> Loading... </strong>
              </div>
            </template>

            <template #cell(id)="row">
              {{ row.item.id }}
            </template>

            <template #cell(created_at)="row">
              {{ new Date(row.item.created_at).toDateString() }}
            </template>

            <template #cell(is_mosque)="row">
              {{ row.item.is_mosque === '1' ? $t('message.yes') : $t('message.no') }}
            </template>

            <template #cell(status)="row">
              {{ row.item.status | status }}
            </template>

            <template #cell(actions)="row">
              <b-button
                v-if="check(row.item.user_id) && row.item.status !== 'A'"
                class="mr-1"
                size="sm"
                variant="primary"
                link
                :to="`/contracts/edit/${row.item.id}`"
              > {{ $t('buttons.edit') }} </b-button>
              <b-button
                class="mr-1"
                size="sm"
                variant="primary"
                @click="showDetails(row.item.id)"
              >
                {{ $t('buttons.details') }}
              </b-button>
              <b-button
                v-if="checkRole(['admin','sheikh']) && row.item.updated_by !== null"
                class="mr-1"
                size="sm"
                variant="success"
                @click="confirmAccepted(row.item.id, 'A')"
              > {{ $t('buttons.active') }} </b-button>
              <b-button
                v-if="checkRole(['admin','sheikh'])"
                class="mr-1"
                size="sm"
                variant="danger"
                @click="confirmReject(row.item.id, 'R')"
              > {{ $t('buttons.rejected') }} </b-button>
              <b-button
                v-if="(check(row.item.user_id) && row.item.status !== 'A') || checkRole(['admin'])"
                size="sm"
                variant="danger"
                @click="confirmDelete(row.item.id)"
              > {{ $t('buttons.delete') }}
              </b-button>
              <b-button
                v-if="checkRole(['sheikh'])"
                class="ml-1"
                size="sm"
                variant="danger"
                @click="showDate(row.item.id)"
              > {{ $t('buttons.date') }} </b-button>
            </template>
          </b-table>
        </b-col>
      </b-row>
      <contract-date :id="contractId" />
      <div class="mt-3">
        <b-pagination
          v-model="currentPage"
          :total-rows="rows"
          align="left"
          :per-page="perPage"
          aria-controls="my-table"
        />
      </div>
    </b-card>
    <company-details :obj="obj" />
  </div>
</template>

<script>
import {
  BRow, BCol, BTable, BButton, BPagination, BCard,
} from 'bootstrap-vue'
import { faEye, faTrash, faPlus } from '@fortawesome/free-solid-svg-icons'
import { library } from '@fortawesome/fontawesome-svg-core'
import AuthService from '@/middleware/index'
import CompanyDetails from './companyDetails.vue'
import ContractDate from './EditContractDate.vue'

library.add(faEye, faTrash, faPlus)

export default {
  name: 'AllContracts',
  components: {
    BCard,
    BRow,
    BCol,
    BTable,
    BButton,
    BPagination,
    CompanyDetails,
    ContractDate,
  },
  data() {
    return {
      contractId: null,
      perPage: 6,
      currentPage: 1,
      add: true,
      obj: {},
      busy: true,
      file: null,
      fields: [
        { key: 'id', label: this.$t('pages.newContracts.id') },
        { key: 'created_at', label: this.$t('pages.newContracts.createdAt') },
        { key: 'is_mosque', label: this.$t('pages.newContracts.isMosque') },
        { key: 'status', label: this.$t('pages.newContracts.status') },
        { key: 'actions', label: this.$t('pages.newContracts.action') },
      ],
    }
  },
  computed: {
    rows() {
      return this.getContracts.length
    },
    getContracts() { return this.$store.state.contract.contracts },
  },
  created() {
    this.fetchPending()
  },
  methods: {
    fetchPending() {
      this.$store.dispatch('contract/fetch', 'Pending')
    },
    check(id) {
      if (AuthService.userId() && id) {
        return (AuthService.userId() === id)
      }
      return true
    },
    checkRole(role) {
      return role.includes(AuthService.role())
    },
    showDate(id) {
      this.$bvModal.show('EditContractDate')
      this.contractId = id
    },
    confirmAccepted(id, status) {
      this.$swal({
        title: this.$t('pages.newContracts.confirmAccepted.title'),
        text: this.$t('pages.newContracts.confirmAccepted.text'),
        icon: 'question',
        showCancelButton: true,
        confirmButtonText: this.$t('message.accept'),
        cancelButtonText: this.$t('message.cancel'),
        customClass: {
          confirmButton: 'btn btn-success',
          cancelButton: 'btn btn-outline-danger ml-1',
        },
        buttonsStyling: false,
      }).then(result => {
        if (result.value) {
          if (status === 'A') {
            const Obj = {
              contract_id: id,
              status,
            }
            this.$store.dispatch('contract/active', Obj).then(() => {
              this.fetchPending()
              this.$swal({
                icon: 'success',
                title: this.$t('pages.newContracts.messageAccepted.title'),
                text: this.$t('pages.newContracts.messageAccepted.text'),
                customClass: {
                  confirmButton: 'btn btn-success',
                },
              })
            })
          }
        }
      })
    },
    confirmReject(id, status) {
      this.$swal({
        title: this.$t('pages.newContracts.confirmReject.title'),
        text: this.$t('pages.newContracts.confirmReject.text'),
        icon: 'question',
        showCancelButton: true,
        confirmButtonText: this.$t('message.accept'),
        cancelButtonText: this.$t('message.cancel'),
        customClass: {
          confirmButton: 'btn btn-danger',
          cancelButton: 'btn btn-outline-danger ml-1',
        },
        buttonsStyling: false,
      }).then(result => {
        if (result.value) {
          if (status === 'R') {
            const Obj = {
              contract_id: id,
              status,
            }
            this.$store.dispatch('contract/active', Obj).then(() => {
              this.fetchPending()
              this.$swal({
                icon: 'success',
                title: this.$t('pages.newContracts.messageReject.title'),
                text: this.$t('pages.newContracts.messageReject.text'),
                customClass: {
                  confirmButton: 'btn btn-danger',
                },
              })
            })
          }
        }
      })
    },
    confirmDelete(id) {
      this.$swal({
        title: this.$t('pages.newContracts.confirmDelete.title'),
        text: this.$t('pages.newContracts.confirmDelete.text'),
        icon: 'question',
        showCancelButton: true,
        confirmButtonText: this.$t('message.accept'),
        cancelButtonText: this.$t('message.cancel'),
        customClass: {
          confirmButton: 'btn btn-danger',
          cancelButton: 'btn btn-outline-primary ml-1',
        },
        buttonsStyling: false,
      }).then(result => {
        if (result.value) {
          this.$store.dispatch('contract/delete', id).then(() => {
            this.fetchPending()
            this.$swal({
              icon: 'success',
              title: this.$t('pages.newContracts.messageDelete.title'),
              text: this.$t('pages.newContracts.messageDelete.text'),
              customClass: {
                confirmButton: 'btn btn-success',
              },
            })
          })
        }
      })
    },
    showDetails(id) {
      // this.obj = obj
      this.$router.push({ name: 'contractsShow', params: { id } })

      // this.$bvModal.show('companyDetails')
    },
  },
}
</script>

